<template>
	<div class="mine-login-nav">
		<i class="logout-bnt" @click="changeClick">切换离线状态</i>
		<i class="logout-bnt logout" @click="logoutClick">退出登录</i>
	</div>
</template>

<script>


export default {
  name: 'MinLoginNav',
  components:{
  },
  methods:{
	changeClick(){
		console.log("改变")
		this.$router.push('/login').catch(res=>{res})
		this.$toast.show('已切换到离线状态',4000)
	},
	//退出登录
	logoutClick(){
		this.$store.commit('logout')	//退出登录
		this.$router.push("/login")
	},
  }
}
</script>


